import React from 'react'

const Web = (props) => {

  let { alt, bigImg } = props;
  return (
    <div> 
      <div className="row">
        <div className="column" data-aos="zoom-in-up">
          <img src={bigImg} alt={alt} style={{ width: "100%" }}/>
        </div>
      </div>
    </div>
  )
}

export default Web
