import React, { useState, useEffect } from 'react';
import Web from './Web';
import Spinner1 from "./Spinner1";
import InfiniteScroll from "react-infinite-scroll-component";
import Footer from './Footer';
import AOS from 'aos';


function Setone() {
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const [photos, setPhotos] = useState([]);
  const [totalresult, setTotalResult] = useState(0);
  const [inputValue, setInputValue] = useState('');

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };


    const handleClick = async (inputValue) => {
      setLoading(true);
      setPage(1);
      const response = await fetch(`https://api.pexels.com/v1/search?query=${encodeURIComponent(inputValue)}&per_page=10&page=1`, {
        headers: {
          Authorization: 'cHDYJQL0d6UCObhyCWqVYGhj3Jpn8QB7I4Dh6OKsqSu7bTZ6emDG6Jlw',
        },
      });
    
      const data = await response.json();
    
      setPhotos(data.photos);
      setTotalResult(data.total_results);
      setLoading(false);
    };
    


// search?query=green
  useEffect(() => {
    const fetchPhotos = async () => {
      setLoading(true)
      const response = await fetch(`https://api.pexels.com/v1/curated?&per_page=10&page=${page}`, {
        headers: {
          Authorization: 'cHDYJQL0d6UCObhyCWqVYGhj3Jpn8QB7I4Dh6OKsqSu7bTZ6emDG6Jlw',
        },
      });

      const data = await response.json();

      setPhotos((prevPhotos) => [...prevPhotos, ...data.photos]);
    };

    fetchPhotos();
    setLoading(false)
  }, [page]);
  
  useEffect(() => {
    AOS.init();
  }, []);

  const handleLoadMore = () => {
    setLoading(true)
    setPage((prevPage) => prevPage + 1);
    setLoading(false)
  };

  return (
    <div className="container my-3">
      <h1 className="text-center mt-5 my-4 mb-5 " data-aos="zoom-in-up" id='heading'>WallPaper Galaxy</h1>
      <form className="d-flex mb-3">
        <input type="text" className="form-control me-2" onChange={handleInputChange}/>
        <button className="btn btn-primary" onClick={handleClick}>Search</button>
      </form>
     
      {loading && <Spinner1 />}
      <InfiniteScroll
        dataLength={photos.length}
        //next={fetchMoreData}
        hasMore={photos.length !== totalresult}
      //loader={<Spinner1 />}
      >
        <div className='container-fluid my-3'>
          <div className="row mb-3">
            {photos.map((photo , index) => {
              return (
                <div className="col-md-3" key={index}>
                  <Web bigImg={photo.src.portrait} alt={photo.alt}/>
                </div>
              )
            })}
          </div>
          <hr/>
          <button type='button' className="btn btn-primary mb-3" onClick={handleLoadMore}>Load More</button>
          <Footer/>
        </div>

      </InfiniteScroll>
    </div>
  );
}


export default Setone;
